.field {
  flex-flow: column-reverse;
  margin-bottom: 1em;
  background: #EFEFEF;
  border-radius: 8px;
  padding-left: 10px;
  padding-right: 10px;
  height: 60px;
  display: flex;
  padding-bottom: 10px;
}
.field input[type=date] {
  font-weight: 500;
  color: #535353;
  font-size: 15px;
}
.field select {
  height: 30px;
  border: none;
  outline: none;
  background: none;
  color: #535353 !important;
  font-size: 16px;
  font-weight: 500;
  padding-left: 8px;
}
.field .select_label {
  margin-bottom: 0px;
  cursor: pointer;
  font-size: 12px;
  color: #a4a3a3;
  font-weight: normal;
  margin-top: 5px;
  margin-left: 8px;
}
.field label,
.field input {
  transition: all 0.2s;
  touch-action: manipulation;
  background: transparent;
  border: none;
  outline: 0px;
  color: #535353;
}
.field input {
  font-size: 16px;
  font-weight: 500;
  padding-left: 0px;
  border: 0px !important;
  cursor: text;
  color: #535353;
  box-shadow: none !important;
  width: 100% !important;
  z-index: 1;
  padding-left: 8px;
}
.field label {
  letter-spacing: 0.05em;
  font-size: 13px;
  color: #2e2e2e;
  font-weight: normal;
  text-transform: unset;
}
.field input:placeholder-shown + label {
  cursor: text;
  transform-origin: left bottom;
  transform: translate(8px, 20px) scale(1.1);
  margin-bottom: 0px;
  font-size: 13px;
  color: #2e2e2e;
  font-weight: normal;
  max-width: 90%;
}
.field ::-webkit-input-placeholder {
  opacity: 0;
  transition: inherit;
}
.field input:focus::-webkit-input-placeholder {
  opacity: 1;
}
.field input:not(:placeholder-shown) + label,
.field input:focus + label {
  transform: translate(8px, 6px) scale(1);
  cursor: pointer;
  margin-bottom: 0px;
  font-size: 12px;
  color: #a4a3a3;
  font-weight: normal;
  max-width: 90%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  z-index: 1;
}

