}
.custom__checkbox:not(:checked)::after,
input[type=radio]:not(:checked)::after {
  border-radius: 50%;
  top: -5px;
  left: -10px;
  position: relative;
  background-color: transparent;
  content: url(../src/assets/images/deactivated-radio-button.png);
  display: block;
  transform: scale(0.69);
}
.custom__checkbox:checked::after,
input[type=radio]:checked::after {
  border-radius: 50%;
  top: -4px;
  left: -10px;
  position: relative;
  background-color: transparent;
  content: url(../src/assets/images/active-radio-button.svg);
  display: block;
  transform: scale(0.85);
}

