.switch_container {
  width: min(346px, 100%);
  height: 35px;
  line-height: 35px;
  background: #efefef;
  color: #a4a3a3;
  border-radius: 10px;
  display: flex;
  justify-content: space-around;
  font-size: 14px;
  font-weight: 500;
  user-select: none;
  position: relative;
  overflow: hidden;
}

.switch_group {
  display: flex;
  align-items: center;
  padding-top: 9px;
  margin-bottom: 35px;
}

.g_label {
  margin-bottom: 0px;
  font-size: 16px;
  font-weight: bold;
  margin-right: 26px;
  color: #2e2e2e;
}

.switchslider {
  position: absolute;
  content: "";
  background: #A3191E;
  width: 50%;
  height: 100%;
  left: 0px;
  top: 0px;
  transition: transform 0.2s ease-in-out;
  transform: translateX(-100%);
}

.switch_container label {
  width: 50%;
  text-align: center;
  z-index: 2;
  margin-bottom: 0px;
}
.switch_container label#option1 {
  border-right: 1px solid #fff;
}

.switch_container #radio1,
.switch_container #radio2 {
  display: none;
}

.switch_container #radio1:checked ~ #option1 {
  color: #fff;
}

.switch_container #radio2:checked ~ #option2 {
  color: #fff;
}

.switch_container #radio1:checked ~ .switchslider {
  transform: translateX(0%);
}

.switch_container #radio2:checked ~ .switchslider {
  transform: translateX(100%);
}

