.progressbar {
  margin: 0;
  padding: 0;
  counter-reset: step;
}

.progressbar li {
  list-style-type: none;
  width: 33%;
  float: left;
  font-size: 12px;
  position: relative;
  text-align: center;
  text-transform: capitalize;
  color: #8b8a8a;
  font-weight: bold;
}
@media (max-width: 575px) {
  .progressbar li {
    font-size: 10px;
  }
}

.progressbar li:before {
  width: 30px;
  height: 30px;
  content: counter(step);
  counter-increment: step;
  line-height: 30px;
  display: block;
  text-align: center;
  margin: 0 auto 10px auto;
  border-radius: 50%;
  background-color: #8b8a8a;
  color: #fff;
  position: relative;
  z-index: 1;
}

.progressbar li:after {
  width: 100%;
  height: 4px;
  content: "";
  position: absolute;
  border-bottom: 2px dotted #c8c8c8;
  top: 14px;
  left: -50%;
  z-index: 0;
}

.progressbar .default_act {
  color: #404040;
}
.progressbar .default_act::before {
  background-color: #000;
}

.progressbar li:first-child:after {
  content: none;
}

.progressbar li.active {
  color: #404040;
}

.progressbar li.active:before {
  background: #404040;
  border: none;
}

.progressbar li.active + li:after {
  border-bottom: 2px dotted #000000;
}

.progressbar li.redactive + li:after {
  border-bottom: 2px dotted #A3191E;
}

.progressbar li.redactive::before {
  background: #A3191E;
  border: none;
  content: "";
  font-family: "Font Awesome 5 Free";
