  grid-template-columns: 1fr 120px;
  padding: 0px 10px;
  gap: 10px;
}

.review_detail_feedback .Chat-row {
  padding: 0px 0px 10px 0;
  border: 0px;
}

.border-feedback:not(:last-child) {
  border-bottom: 1px dashed #cecece;
  margin-bottom: 10px;
}

.review__feedback {
  border: 1px dashed #cecece;
  padding: 10px;
  border-radius: 7px;
}

@media only screen and (max-width: 500px) {
  .review_detail_feedback .flex {
    flex-direction: column;
  }
  .review_detail_feedback .user-text {
    margin-top: 20px;
  }
  .review_detail_feedback .Userimg-div {
    padding: 0;
  }
}
.viewdocument {
  max-width: 90vw !important;
  position: static;
  border-radius: 10px;
  overflow: hidden;
}
@media only screen and (min-width: 576px) {
  .viewdocument {
    min-height: 90vh;
    max-height: 90vh;
  }
}
.viewdocument .mat-dialog-container {
  padding: 0px;
  position: relative;
}
.viewdocument .modal__header {
  position: absolute;
  right: 0px;
  top: 10px;
  z-index: 2;
}
.viewdocument .modal__header .Clobtn {
  font-size: 20px;
  color: #fff;
  opacity: 1;
  font-weight: normal;
}

.checkbook .modal__header .Clobtn {
  color: #333;
}

@media (min-width: 1200px) {
  .container {
    max-width: 1038px;
  }
}
.categories_list {
  overflow: auto;
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
  max-width: 337px;
}
.categories_list::-webkit-scrollbar {
  display: none;
}
.categories_list .list_container {
  padding: 11px;
  display: grid;
  grid-template-columns: 50px 1fr;
  align-items: center;
  gap: 35px;
}
.categories_list .list_container:not(:last-child) {
  border-bottom: 1px dashed #cecece;
}
.categories_list .list_container p {
  font-size: 16px;
  color: #535353;
  margin-bottom: 0px;
}

.modal.right.categorey .modal-lg .modal-body {
  padding-bottom: 10px;
}

.mat-option {
  height: 30px !important;
}

.filter {
  text-align: right;
  padding: 10px 20px;
  background: #F2F2F2;
  display: flex;
  justify-content: space-between;
  margin-top: 5px;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.5);
}
.filter button:not(.primary) {
  border: 0px;
  background-color: transparent;
  color: #A3191E;
}
.filter .apply {
  padding: 5px 20px;
  border-radius: 22px;
}
.filter input[type=text] {
  width: 100%;
}
.filter .dropdown-menu {
  border: none;
  right: -5px;
  padding: 16px;
  left: unset;
  top: 29px;
}
.filter .dropdown-menu::before {
  width: 15px;
  height: 15px;
  content: "";
  right: 11px;
  top: -8px;
  z-index: 22222;
  background: #fff;
  position: absolute;
  transform: rotate(45deg);
}
.filter .dropdown-menu li {
  padding: 5px 0px;
}
.filter .dropdown-menu li:not(:last-child) {
  border-bottom: 1px dashed #c8c8c8;
  padding: 20px 0px;
}
.filter .dropdown-menu li:not(:first-child) {
  padding-top: 20px;
}
.filter .dropdown-menu li .filter_date {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-top: 5px;
}
.filter .dropdown-menu li .filter_date label {
  font-weight: normal;
}
.filter .dropdown-menu li .filter_date input[type=date] {
  width: unset;
  padding-right: 10px;
}
.filter .dropdown-menu li select {
  width: 100%;
  border-radius: 22px;
  height: 30px;
  padding: 0px 10px;
  border: 1px solid #636363;
}

.comment textarea {
  resize: none;
}

.breadcrumb_container {
  background: #F2F2F2;
  margin-bottom: 20px;
}
.breadcrumb_container .breadcrumb {
  padding: 16px;
  background: transparent;
  margin-bottom: 0px;
}
.breadcrumb_container .breadcrumb .breadcrumb-item {
  color: #535353;
  font-weight: 600;
}
.breadcrumb_container .breadcrumb .breadcrumb-item a {
  color: #535353;
  text-decoration: underline;
  font-weight: 500;
}

.breadcrumb > li + li:before {
  padding: 0 5px;
  color: #535353;
  font-family: "Font Awesome 5 Free";
  content: "";
}

.invalid-feedback {
  color: #A3191E;
}